import { Link } from '@remix-run/react';
import { json, useLoaderData } from 'superjson-remix';
import { useState } from 'react';

export async function loader() {
  const res = await fetch(
    'https://restcountries.com/v3.1/all?fields=flags,name,population,region,capital'
  );
  return json(await res.json());
}
export default function Index() {
  const countries = useLoaderData();

  // Search bar
  const [query, setQuery] = useState('');

  return (
    <section className='container mx-auto max-w-screen-lg'>
      <section className='flex'> 
      <div className='mt-11'>
        <input className='h-10'
          placeholder="Search for a country..."
          onChange={(event) => setQuery(event.target.value)}
        />
      </div>
      <div className='ml-auto mt-11'>
        <input className='h-10' placeholder="Filter by region" />
      </div>
      </section>
      

      <div className='grid grid-cols-4 gap-y-32 mt-11 place-content-between'>
        {countries
          .filter(({ name }) => {
            if (name === '') {
              return countries;
            } else if (
              name.common.toLowerCase().includes(query.toLowerCase())
            ) {
              return countries;
            }
          })
          .map(({ flags, name, population, region, capital }) => (
            <div key={name.common}>
              <img
                className="h-32 w-3/4"
                src={flags.png}
                alt={`bandeira de ${name.common}`}
              />
              <h2>{name.common}</h2>
              <p>
                <b>Population:</b> {population}
              </p>
              <p>
                <b>Region:</b> {region}
              </p>
              <p>
                <b>Capital:</b> {capital}
              </p>
            </div>
          ))
          .splice(0, 8)}
      </div>
    </section>
  );
}
